@font-face {
  font-family: GlegooRegular;
  src: url(../fonts/Glegoo-Regular.ttf);
}
html {
  line-height: 1.15;
  color: #000000de;
}
html body {
  font-family: GlegooRegular;
  margin: 0 0 40px 0;
}
html body h3 {
  font-weight: 400;
  font-size: 2.92rem;
  margin: 1.94666667rem 0 1.168rem 0;
}
html body h5 {
  font-weight: 400;
  font-size: 1.64rem;
  margin: 1.09333333rem 0 0.656rem 0;
}
html body .container {
  margin: 0 auto;
  max-width: 1280px;
  width: 90%;
}
html body .container .left {
  float: left !important;
}
html body .container .right {
  float: right !important;
}
html body .container ul:not(.browser-default) {
  padding-left: 0;
  list-style-type: none;
}
html body .container ul:not(.browser-default) > li {
  list-style-type: none;
}
html body .container a {
  color: #039be5;
  text-decoration: none;
}
@media only screen and (min-width: 0) {
  html {
    font-size: 14px;
  }
}
@media only screen and (min-width: 601px) {
  body .container {
    width: 85%;
  }
}
@media only screen and (min-width: 992px) {
  html {
    font-size: 14.5px;
  }
  html body .container {
    width: 70%;
  }
}
@media only screen and (min-width: 1200px) {
  html {
    font-size: 15px;
  }
}
nav {
  height: 64px;
  line-height: 64px;
  margin-bottom: 10px;
  box-shadow: 0 2px 2px 0 #00000020;
  background-color: #ee6e73;
  width: 100%;
}
nav .container {
  position: relative;
  height: 100%;
}
nav .container img {
  height: 64px;
}
nav .container ul {
  margin: 0;
}
nav .container ul li {
  float: left;
}
nav .container ul li a {
  transition: background-color 0.3s;
  color: #fff;
  display: block;
  padding: 0 15px;
}
nav .container ul li a:hover,
nav .container ul li a.active {
  background-color: #00000020;
}
table {
  width: 100%;
  border: none;
  display: table;
  border-collapse: collapse;
}
table thead tr {
  border-bottom: 1px solid #00000020;
}
table thead tr th {
  cursor: pointer;
  padding: 16px;
  text-align: left;
}
table thead tr th i {
  display: none;
  border: solid #000000;
  border-width: 0 3px 3px 0;
  padding: 6px;
  float: right;
}
table thead tr th i.asc {
  display: block;
  margin-top: 5px;
  transform: rotate(-135deg);
}
table thead tr th i.des {
  display: block;
  margin-bottom: 5px;
  transform: rotate(45deg);
}
table tbody tr {
  border-bottom: 1px solid #00000020;
}
table tbody tr td {
  padding: 16px;
}
table tbody tr:hover {
  background-color: #f2f2f2;
}
.collection {
  width: 100%;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
  overflow: hidden;
}
.collection .collection-header {
  background-color: #fff;
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 20px;
}
.collection .collection-item {
  background-color: #fff;
  padding: 10px 20px;
  border-bottom: 1px solid #e0e0e0;
}
.collection .collection-item:last-child {
  border-bottom: none;
}
.x:before {
  content: "";
  background: url("../img/x-mark.svg") no-repeat;
  position: relative;
  width: 17px;
  height: 17px;
  top: 0;
  left: 0;
  display: inline-block;
}
.check:before {
  content: "";
  background: url("../img/checked.svg") no-repeat;
  position: relative;
  width: 17px;
  height: 17px;
  top: 0;
  left: 0;
  display: inline-block;
}



